import React from 'react';
import Header from './components/Header';
import Form from './components/Form';
import Filters from './components/Filters';
import Card from './components/Card';
import Footer from './components/Footer';
import './App.css';

const INITIAL_STATE = {
  cardName: '',
  cardDescription: '',
  cardAttr1: '0',
  cardAttr2: '0',
  cardAttr3: '0',
  cardImage: '',
  cardRare: 'Normal',
  cardTrunfo: false,
  hasTrunfo: false,
  isSaveButtonDisabled: true,
  savedCards: [],
  searchBoxData: '',
  rarityFilter: 'Todas'
};

class App extends React.Component {
  constructor() {
    super();
    this.state = INITIAL_STATE;
  }

  componentDidMount() {
    const userSavedCards = localStorage.getItem('savedCards');
    const userHasTrunfo = localStorage.getItem('hasTrunfo');

    if (userSavedCards !== null) {
      this.setState({ savedCards: JSON.parse(userSavedCards) });
    }
    if (userSavedCards !== null) {
      this.setState({ hasTrunfo: JSON.parse(userHasTrunfo) });
    }
  }

  verifyAllInputs = () => { // FunÃ§Ã£o que habilita o botÃ£o 'salvar', quando todos os campos do formulÃ¡rio forem preenchidos corretamente. SerÃ¡ chamada dentro da funÃ§Ã£o onInputChange(), que Ã© chamada dentro de escutadores do tipo 'onChange'.
    const {
      cardName,
      cardDescription,
      cardAttr1,
      cardAttr2,
      cardAttr3,
      cardImage,
      cardRare,
    } = this.state;

    const attrSum = Number(cardAttr1) + Number(cardAttr2) + Number(cardAttr3);
    const attrSumMaxValue = 210;
    const attrMin = 0;
    const attrMax = 90;

    if ((cardName.length >= 1)
    && (cardDescription.length >= 1)
    && (cardImage.length >= 1)
    && (cardRare.length >= 1)
    && (Number(cardAttr1) >= attrMin && Number(cardAttr1) <= attrMax)
    && (Number(cardAttr2) >= attrMin && Number(cardAttr2) <= attrMax)
    && (Number(cardAttr3) >= attrMin && Number(cardAttr3) <= attrMax)
    && (attrSum <= attrSumMaxValue)) {
      return this.setState({
        isSaveButtonDisabled: false,
      });
    }
    return this.setState({
      isSaveButtonDisabled: true,
    });
  }

  onInputChange = ({ target }) => { // FunÃ§Ã£o que altera o valor de qualquer estado, sempre que um input for realizado no elemento onde ela estÃ¡ sendo chamada. || OBS: Para que tal funÃ§Ã£o funcione, os 'name' de cada um dos elementos do Forms devem ser iguais ao nome dos estados.
    const { name } = target;
    const formElementValue = target.type === 'checkbox' ? target.checked : target.value;

    this.setState({ // Aprendi a utilizar o 2Â° parÃ¢metro da setState() na matÃ©ria "Ciclo de Vida dos Componentes", ainda nÃ£o dada. Em suma, assim que o estado for atualizado, a callback passada como argumento, serÃ¡ rodada.
      [name]: formElementValue,
    }, this.verifyAllInputs);
  }

  onSaveButtonClick = () => {
    // FunÃ§Ã£o que altera o estado 'hasTrunfo', de false para true, quando uma carta do tipo trunfo Ã© salva.
    // FunÃ§Ã£o que salva as informaÃ§Ãµes de uma respectiva carta, como um objeto dentro do array contido no estado 'savedCards'.
    // FunÃ§Ã£o que limpa os campos do formulÃ¡rio e o preview da carta.
    const {
      cardName,
      cardDescription,
      cardAttr1,
      cardAttr2,
      cardAttr3,
      cardImage,
      cardRare,
      cardTrunfo,
    } = this.state;

    const savedCardInfo = {
      cardName,
      cardDescription,
      cardAttr1,
      cardAttr2,
      cardAttr3,
      cardImage,
      cardRare,
      cardTrunfo,
    };

    if (cardTrunfo === true) {
      this.setState({ hasTrunfo: true }, () => localStorage.setItem('hasTrunfo', true));
    }

    this.setState((prevState) => ({
      savedCards: [...prevState.savedCards, savedCardInfo],
      cardName: '',
      cardDescription: '',
      cardAttr1: '0',
      cardAttr2: '0',
      cardAttr3: '0',
      cardImage: '',
      cardRare: 'Normal',
      cardTrunfo: false,
      isSaveButtonDisabled: true,
    }), () => localStorage.setItem('savedCards', JSON.stringify(this.state.savedCards))); // Salvando o estado 'savedCards', no Local Storage.
  }

  deleteDisplayedCard = ({ target }) => { // FunÃ§Ã£o que possibilita a exclusÃ£o de uma carta criada anteriormente.
    const { savedCards } = this.state;
    const displayedCardId = target.id;

    const toBeDel = savedCards.find((cardInfo) => cardInfo.cardName === displayedCardId); // Retorna o objeto com as informaÃ§Ãµes da carta recÃ©m-excluÃ­da.
    const newSC = savedCards.filter((cardInfo) => cardInfo.cardName !== displayedCardId); // Retorna o array contido no estado 'savedCards', porÃ©m sem o objeto referente Ã  carta que foi excluÃ­da.

    if (toBeDel.cardTrunfo === true) {
      this.setState({
        savedCards: newSC,
        hasTrunfo: false,
      }, () => {
        localStorage.setItem('savedCards', JSON.stringify(newSC));
        localStorage.setItem('hasTrunfo', false)
      }); // Salvando o estado 'savedCards', apÃ³s a deleÃ§Ã£o, no Local Storage.
    }
    this.setState({ savedCards: newSC }, localStorage.setItem('savedCards', JSON.stringify(newSC)));
    // OBS: Caso a carta 'Super Trunfo' seja excluÃ­da, alÃ©m de atualizar o estado 'savedCards' com o novo array de objetos 'newSC', o estado 'hasTrunfo' voltarÃ¡ a ser igual a False. Isso Ã© importante para que o usuÃ¡rio seja capaz de criar uma nova carta do tipo 'Super Trunfo'.
  }

  filtersFunction = () => { // Tal funÃ§Ã£o sempre retornarÃ¡ um array de objetos, que acumula informaÃ§Ãµes de cada uma das cartas. Por outro lado, dependendo dos filtros aplicados pelo usuÃ¡rio, o respectivo array apresentarÃ¡ uma quantidade reduzida de objetos.
    const { savedCards, searchBoxData, rarityFilter } = this.state;

    if (searchBoxData.length >= 1) {
      return savedCards.filter(({ cardName }) =>
        cardName.toLowerCase().includes(searchBoxData.toLowerCase())); // Retornado quando o usuÃ¡rio pesquisar por uma determinada carta na searchbox.
    }
    if (rarityFilter === 'Normal') {
      return savedCards.filter(({ cardRare }) => cardRare === 'Normal'); // Retornado quando o usuÃ¡rio selecionar 'Normal', no dropdown.
    }
    if (rarityFilter === 'Raro') {
      return savedCards.filter(({ cardRare }) => cardRare === 'Raro'); // Retornado quando o usuÃ¡rio selecionar 'Raro', no dropdown.
    }
    if (rarityFilter === 'Muito Raro') {
      return savedCards.filter(({ cardRare }) => cardRare === 'Muito Raro'); // Retornado quando o usuÃ¡rio selecionar 'Muito Raro', no dropdown.
    }
    return savedCards; // Retornado quando o usuÃ¡rio nÃ£o aplicar nenhum filtro.

    // OBS: A funÃ§Ã£o filtersFunction() serÃ¡ chamada dentro da <div><div /> "cardsDisplay", acompanhada de um .map().
  }

  render() {
    const {
      cardName,
      cardDescription,
      cardAttr1,
      cardAttr2,
      cardAttr3,
      cardImage,
      cardRare,
      cardTrunfo,
      hasTrunfo,
      isSaveButtonDisabled,
      savedCards,
      searchBoxData,
      rarityFilter,
    } = this.state;

    return (
      <div className="app">
        <Header />
        <main>
          <section id="cardDevelopmentContainer">
            <div id="formContainer">
              <h3>Estruturar Nova Carta</h3>
              <Form
                cardName={ cardName } cardDescription={ cardDescription }
                cardAttr1={ cardAttr1 } cardAttr2={ cardAttr2 }
                cardAttr3={ cardAttr3 } cardImage={ cardImage }
                cardRare={ cardRare } cardTrunfo={ cardTrunfo }
                hasTrunfo={ hasTrunfo } isSaveButtonDisabled={ isSaveButtonDisabled }
                onInputChange={ this.onInputChange } onCardTrunfoClick={ this.onCardTrunfoClick }
                onSaveButtonClick={ this.onSaveButtonClick }
              />
            </div>
            <div id="cardPreviewContainer">
              <h3>Preview da Nova Carta</h3>
              <div id="cardPrevOutsideBorder">
                <Card
                  cardName={ cardName } cardDescription={ cardDescription }
                  cardAttr1={ cardAttr1 } cardAttr2={ cardAttr2 }
                  cardAttr3={ cardAttr3 } cardImage={ cardImage }
                  cardRare={ cardRare } cardTrunfo={ cardTrunfo }
                />
              </div>
            </div>
          </section>
          <section id="savedCardsContainer">
            <Filters 
              searchBoxData={ searchBoxData } onInputChange={ this.onInputChange }
              rarityFilter={ rarityFilter }
            />
            <div id="cardsDisplay">
              { savedCards !== undefined 
                && this.filtersFunction().map((cardInfo) => (
                <div key={ cardInfo.cardName } className="eachDisplayedCard">
                  <Card
                    cardName={ cardInfo.cardName } cardDescription={ cardInfo.cardDescription }
                    cardAttr1={ cardInfo.cardAttr1 } cardAttr2={ cardInfo.cardAttr2 }
                    cardAttr3={ cardInfo.cardAttr3 } cardImage={ cardInfo.cardImage }
                    cardRare={ cardInfo.cardRare } cardTrunfo={ cardInfo.cardTrunfo }
                  />
                  <button
                    id={ cardInfo.cardName }
                    className="deleteBtn"
                    type="button"
                    onClick={ this.deleteDisplayedCard }
                  >
                    Excluir
                  </button>
                </div>
              ))}
            </div>
          </section>
        </main>
          <Footer />
      </div>
    );
  }
}

export default App;

